.filtersContainer {
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  flex-wrap: wrap;
}
.categoriesFilter, .brandsFilter, .levelsFilter {
  width: 47%;
  margin-bottom: 20px;
  max-height: 300px;
}
.filterHeader, .levelsFilter {
  height: 40px;
  background-color: #DFDFDF;
  display: flex;
  align-items: center;
}
.levelsFilter {
  justify-content: space-around;
  padding: 3px 0;
  border: 2px solid #DFDFDF;
}
.levelsFilter button {
  height: 100%;
  width: 25%;
  font-weight: 700;
  background-color: #ffffff;
  border: none;
}
.levelsFilter button:focus {
  outline: none;
}
.levelsFilter .selectedLevel {
  border: 2px solid black;
  background-color: #6ad12e;
}
.filterIcon {
  width: 50px;  
  height: 30px;
}
.filterIcon img {
  width: 30px;
}

@media(max-width: 600px) {
  .categoriesFilter, .brandsFilter, .levelsFilter {
    width: 95%; 
  }
  .filtersContainer {
    justify-content: center;
  }
}
@media(min-width: 1400px) {
  .categoriesFilter, .brandsFilter, .levelsFilter {
    width: 48%;
    margin-bottom: 20px;
    max-height: 300px;
  }
}